import React, { useState } from 'react';
import Projects from './projects';
import Resume from './resume';
import Contacts from './contact';
import AboutMe from './aboutMe';

function Main() {
    const [page, setPage] = useState('');
    // setPage('aboutMe')
    console.log(page)
    const pathFunction = () => {
        if (page === 'resume') { return (<Resume />) }
        else if (page === 'projects') { return <Projects /> }
        else if (page === 'contacts') { return <Contacts /> }
        else if (page === 'aboutMe') { return <AboutMe /> }
        else { return <AboutMe /> }
    }


    return (

        < div className="main" class="d-flex justify-content-center col-12" >
            <nav class="navbar navbar-expand-lg navbar-light bg-light">

                <div class="collapse navbar-collapse" id="navbarNav">
                    <ul class="navbar-nav">
                        <li class="nav-item active">
                            <button type="button" class="btn btn-primary" onClick={() => setPage('aboutMe')}>About Me</button>
                        </li>
                        <li class="nav-item active">
                            <button type="button" class="btn btn-primary" onClick={() => setPage('projects')}>Projects</button>
                        </li>
                        <li class="nav-item active">
                            <button type="button" class="btn btn-primary" onClick={() => setPage('resume')}>Resume</button>
                        </li>
                        <li class="nav-item active">
                            <button type="button" class="btn btn-primary" onClick={() => setPage('contacts')}>Contacts</button>
                        </li>

                    </ul>
                </div>
            </nav>

            {pathFunction}
        </div >
    )


}

export default Main